import React, { Component } from "react";
import Typography from "@material-ui/core/Typography";
import Button from "../Controls/Button/Button";
import "./previewInstructionsVerbal.css";
import birballogo from "../../assets/images/Assessment/login_page_logo.png";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import { secondsToHours } from "../../utils/utility";



export class PreviewInstructionsMCQ extends Component {
  constructor(props) {
    super(props);

    this.state = {
   mcqQuestions:[],
   totalTime:''
    };
  }

  componentDidMount(){
    if( this.props.mcqQuestions){
   this.setState({
     mcqQuestions: this.props.mcqQuestions,
  
   })

   let totalTime=0
   this.props.mcqQuestions.map(item=>{
     totalTime+= item.id && item.id.suggestedDuration
   })

   this.setState({
     totalTime:totalTime
   })
  
      }

  }





  render() {


    return (
      <div style={{height:'100%',width:'100%'}}>
      <div className="instruction-main-div">
         
         <div className="main-wrapper">
         <div className="left-content">
          <div className="instructions-heading">
            <Typography>Instructions</Typography>
          </div>
          <div className="instructions-subHeading">
            <Typography>Read and follow the given Instructions</Typography>
          </div>
          </div>

          <div className="right-content">
          <div className="instructions-logo">
            <img src={birballogo}></img>
          </div>
          </div>
         </div>

          <div className="page-content">
            
            <div className="contents-verbal">
            <div>
        <div className="li-2">
         <div className="dot"></div> Question Type : MCQ
        </div>
        <div className="li-2">
        <div className="dot"></div>Total No. of Questions : {this.state.mcqQuestions.length}
        </div>
        <div className="li-2">
        <div className="dot"></div> Total Time : {secondsToHours(this.state.totalTime)}
        </div>
     
      </div>
  

            </div>

            <div  className="continue-button-wrapper">
                <Button className="continue-button"  disabled={true}>
                    Continue
                </Button>
            </div>

           
          </div>
          
      
        
      </div>
     
      </div>
    );
  }
}

export default PreviewInstructionsMCQ;
