import React, { Component } from "react";
import Typography from "@material-ui/core/Typography";
import Button from "../Controls/Button/Button";
import "./previewVerbalInterview.css";
import CheckIcon from "@material-ui/icons/Check";
import birballogo from "../../assets/images/Assessment/login_page_logo.png";
import audio from '../../assets/images/Assessment/audio.png';
import LinearProgress from '@material-ui/core/LinearProgress';
import Stepper from "../Atoms/Stepper";
import CircularProgress from "@material-ui/core/CircularProgress";
import Box from "@material-ui/core/Box";
import ControlledCheckbox from "../Atoms/Checkbox";
import PreviewImage from '../../assets/images/Assessment/PreviewImage.png';



export class PreviewMCQInterview extends Component {

  state = {
    activeStep:  this.props.mcqIndex,
    mcqQuestions: [],
    mcqQuestion: [],
    options: []

  }

  componentDidMount() {
    if (this.props.mcqQuestions) {
      this.setState({
        mcqQuestions: this.props.mcqQuestions,
        mcqQuestion: this.props.mcqQuestions[0].id && this.props.mcqQuestions[0].id.text,
        options: this.props.mcqQuestions[0].id && this.props.mcqQuestions[0].id.metadata && this.props.mcqQuestions[0].id.metadata.option

      })





    }

  }





  handleStep = (step) => () => {
    // if(step < this.state.activeStep){
    // this.setState({activeStep:step});
    // } 
  }

  getSteps = () => {
    return ['Verbal Questions', 'MCQ'];
  }
  getSteps2 = () => {
    return ['MCQ', 'Verbal Questions'];
  }

  CircularProgressWithLabel = (props) => {
    return (
      <Box position="relative" display="inline-flex">
        <CircularProgress variant="determinate" value={75} />
        <Box
          top={0}
          left={0}
          bottom={0}
          right={0}
          position="absolute"
          display="flex"
          alignItems="center"
          justifyContent="center"
        >
          <Typography
            variant="caption"
            component="div"
            style={{ color: '#04724d', fontWeight: '600', fontSize: '15px' }}
          >{`0:25`}</Typography>
        </Box>
      </Box>
    );
  }






  render() {


    return (
      <div className="preview-main-div-new">

        <div className="interview-flex-div">
          {
            this.props.noShow ? <div className="interviewStepper"> </div>  : this.props.mcqIndex > this.props.verbalIndex ? <div className="interviewStepper">
              <Stepper getSteps={this.getSteps} activeStep={this.state.activeStep} handleStep={this.handleStep} labelClassName="step-label" className='previewMainstepper' />
            </div> : <div className="interviewStepper">
              <Stepper getSteps={this.getSteps2} activeStep={this.state.activeStep} handleStep={this.handleStep} labelClassName="step-label" className='previewMainstepper' />
            </div>
          }
          <div className="interviewStepperNew">
            <div className="inter-2 total-questions">
              <div >
                <div className="questions-wrapper">
                  <div className="total">  Total Questions</div>
                  <div className="circle-interview">{this.state.mcqQuestions.length}</div>
                </div>

              </div>
            </div>
            <div className="timer-wrapper">
              {this.CircularProgressWithLabel()}
            </div>
          </div>
        </div>

        <div className="page-content-preview-2 ">
          <div className="row-wrapper">
            <div className="left-div-new-2">
              <div className="video-div">
                <div className="interview-video">
                  <div className="blank-div-2">
                    <img src={PreviewImage} />
                  </div>
                </div>
              </div>
              <div className="second-wrapper">
                <div className="birbal-logo-preview2">
                  <img style={{ maxWidth: '100px' }} src={birballogo}></img>
                </div>
                <div className="turn-on-2">
                  Please turn on the Video and mic before starting the interview.
                </div>

                <div className="main-flex">
                  <div style={{ display: "flex" }} className="tick-div">

                    <div className="flex-div-preview">

                      <div style={{ backgroundColor: '#04724D' }} className="icon-box"><CheckIcon style={{ color: "#fff", font: "bold", height: '10px', width: '10px' }} /></div>
                      {" "}
                      <div className="access-text"> Audio Access</div>
                    </div>


                    <div className="flex-div-preview">

                      <div style={{ backgroundColor: '#04724D' }} className="icon-box"><CheckIcon style={{ color: "#fff", font: "bold", height: '10px', width: '10px' }} /></div>
                      {" "}
                      <div className="access-text">Video Access</div>
                    </div>

                  </div>
                  <div className="audio-bar">
                    <div className="flex-div-preview-2" >
                      <div className="div-1"> <img src={audio} /> </div>
                      <div className="div-2"> <LinearProgress
                        className="linearProgressInternetLanding"
                        variant="determinate"
                        value={30}
                        style={{
                          backgroundColor: '#fff',
                          height: '10px',

                        }}
                      /> </div>
                    </div>
                  </div>
                </div>

                <div className="main-flex">
                  <div style={{ display: "flex" }} className="tick-div">
                    <div className="flex-div-preview">

                      <div style={{ backgroundColor: '#04724D' }} className="icon-box"><CheckIcon style={{ color: "#fff", font: "bold", height: '10px', width: '10px' }} /></div>
                      {" "}
                      <div className="access-text">Lighting Conditions</div>
                    </div>

                    <div className="flex-div-preview">

                      <div style={{ backgroundColor: '#04724D' }} className="icon-box"><CheckIcon style={{ color: "#fff", font: "bold", height: '10px', width: '10px' }} /></div>
                      {" "}
                      <div className="access-text">Internet Speed</div>
                    </div>
                  </div>
                </div>




              </div>


            </div>
            <div className="preview-start-new previews-subHeading2 right-div-new-2">

              <div className="question-read">Read Your Question</div>
             <div className="content"> <Typography>
                <span className="question-number">Q.1</span>
                <div   dangerouslySetInnerHTML={{ __html: this.state.mcqQuestion }}></div>
              </Typography></div>
              <div className="interview-options-div">
                <div className="flex-div-1">
                  <div className="checkbox-div"><ControlledCheckbox checked={this.state.options[0] && this.state.options[0].correct}
                    size={15}
                    labelStyle={{
                      marginLeft: 15, userSelect: "none", fontSize: '9px',
                      fontWeight: '500'
                    }}
                    borderRadius={2}
                    borderColor={"#04724d"}
                    label={this.state.options[0] && this.state.options[0].text} handleChange={() => {

                    }} /></div>
                  <div className="checkbox-div"><ControlledCheckbox checked={this.state.options[1] && this.state.options[1].correct}
                    size={15}
                    labelStyle={{
                      marginLeft: 15, userSelect: "none", fontSize: '9px',
                      fontWeight: '500'
                    }}
                    borderRadius={2}
                    borderColor={"#04724d"}
                    label={this.state.options[1] && this.state.options[1].text} handleChange={() => {

                    }} /></div>
                </div>
                <div className="flex-div-1">
                  {
                    this.state.options[2] ? <div className="checkbox-div"><ControlledCheckbox checked={this.state.options[2] && this.state.options[2].correct}
                      size={15}
                      labelStyle={{
                        marginLeft: 15, userSelect: "none", fontSize: '9px',
                        fontWeight: '500'
                      }}
                      borderRadius={2}
                      borderColor={"#04724d"}
                      label={this.state.options[2] && this.state.options[2].text} handleChange={() => {

                      }} /></div> : null
                  }
                  {
                    this.state.options[3] ? <div className="checkbox-div"><ControlledCheckbox checked={this.state.options[3] && this.state.options[3].correct}
                      size={15}
                      labelStyle={{
                        marginLeft: 15, userSelect: "none", fontSize: '9px',
                        fontWeight: '500'
                      }}
                      borderRadius={2}
                      borderColor={"#04724d"}
                      label={this.state.options[3] && this.state.options[3].text} handleChange={() => {

                      }} /></div> : null
                  }
                </div>

              </div>

            </div>
          </div>
        </div>
        {/* <div style={{display:'flex',justifyContent:'end',marginRight:'30px'}} className="birbal-logo-preview">
<img style={{maxWidth:'100px'}} src={birballogo}></img>
</div> */}
      </div>
    );
  }
}


export default PreviewMCQInterview;
