import React, { Component } from "react";
import Typography from "@material-ui/core/Typography";
import Button from "../Controls/Button/Button";
import "./PreviewVerbalInterviewMobile.css";
import audio from '../../assets/images/Assessment/audio.png';
import LinearProgress from '@material-ui/core/LinearProgress';
import CircularProgress from "@material-ui/core/CircularProgress";
import Box from "@material-ui/core/Box";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import PreviewImage from '../../assets/images/Assessment/PreviewImage.png';
import ControlledCheckbox from "../Atoms/Checkbox";



export class PreviewVerbalInterview extends Component {

  state = {
    activeStep: 0,
    mcqQuestions: [],
    mcqQuestion: [],
    options: []

  }

  componentDidMount() {
    if (this.props.mcqQuestions) {
      this.setState({
        mcqQuestions: this.props.mcqQuestions,
        mcqQuestion: this.props.mcqQuestions[0].id && this.props.mcqQuestions[0].id.text,
        options: this.props.mcqQuestions[0].id && this.props.mcqQuestions[0].id.metadata && this.props.mcqQuestions[0].id.metadata.option

      })





    }

  }



  handleStep = (step) => () => {
    // if(step < this.state.activeStep){
    // this.setState({activeStep:step});
    // } 
  }

  getSteps = () => {
    return ['Verbal Questions', 'MCQ'];
  }

  CircularProgressWithLabel = (props) => {
    return (
      <Box position="relative" display="inline-flex">
        <CircularProgress variant="determinate" value={75} />
        <Box
          top={0}
          left={0}
          bottom={0}
          right={0}
          position="absolute"
          display="flex"
          alignItems="center"
          justifyContent="center"
        >
          <Typography
            variant="caption"
            component="div"
            style={{ color: '#04724d', fontWeight: '600', fontSize: '11px' }}
          >{`0:25`}</Typography>
        </Box>
      </Box>
    );
  }






  render() {


    return (
      <div className="mobile-preview-main-div-new">


        <div className="mobile-page-content-preview-2 ">
          <div className="mobile-row-wrapper">
            <div className="mobile-left-div-new-2">
              <div className="mobile-video-div">
                <div className="mobile-interview-video">
                  <div className="mobile-blank-div-2">
                    <img src={PreviewImage} />
                  </div>
                </div>
              </div>



            </div>
            <div className="mobile-padding-div">
              <div className="mobile3-audio-bar">
                <div className="mobile3-flex-div-preview-2" >
                  <div className="mobile3-div-1"> <img src={audio} /> </div>
                  <div className="mobile3-div-2"> <LinearProgress
                    className="mobile3-linearProgressInternetLanding"
                    variant="determinate"
                    value={70}
                    style={{
                      backgroundColor: '#fff',
                      height: '5px',
                      color: '#04724d'

                    }}
                  />  </div>
                </div>
              </div>

              <div className="mobile-interview-flex-div">
                <div className="mobile-interviewStepperNew">
                  <div className="mobile-inter-2 total-questions">
                    <div >
                      <div className="mobile-questions-wrapper">
                        <div className="mobile-total">  Total Questions</div>
                        <div className="mobile-circle-interview">{this.state.mcqQuestions.length}</div>
                      </div>

                    </div>
                  </div>
                  <div className="mobile-timer-wrapper">
                    {this.CircularProgressWithLabel()}
                  </div>
                </div>
              </div>
              <div className="mobile-preview-start-new mobile-previews-subHeading2 mobile-right-div-new-2">

                <Typography>
                  <span className="mobile-question-number">Q.1</span>
                  <div   dangerouslySetInnerHTML={{ __html: this.state.mcqQuestion }}></div>
                </Typography>
                <div className="mobile-interview-options-div">
                  <div className="mobile-flex-div-1">
                    <div className="mobile-checkbox-div"><ControlledCheckbox checked={this.state.options[0] && this.state.options[0].correct}
                      size={15}
                      labelStyle={{
                        marginLeft: 15, userSelect: "none", fontSize: '9px',
                        fontWeight: '500'
                      }}
                      borderRadius={2}
                      borderColor={"#04724d"}
                      label={this.state.options[0] && this.state.options[0].text} handleChange={() => {

                      }} /></div>
                    <div className="mobile-checkbox-div"><ControlledCheckbox checked={this.state.options[1] && this.state.options[1].correct}
                      size={15}
                      labelStyle={{
                        marginLeft: 15, userSelect: "none", fontSize: '9px',
                        fontWeight: '500'
                      }}
                      borderRadius={2}
                      borderColor={"#04724d"}
                      label={this.state.options[1] && this.state.options[1].text} handleChange={() => {

                      }} /></div>
                  </div>
                  <div className="mobile-flex-div-1">
                    {
                      this.state.options[2] ? <div className="mobile-checkbox-div"><ControlledCheckbox checked={this.state.options[2] && this.state.options[2].correct}
                      size={15}
                      labelStyle={{
                        marginLeft: 15, userSelect: "none", fontSize: '9px',
                        fontWeight: '500'
                      }}
                      borderRadius={2}
                      borderColor={"#04724d"}
                      label={this.state.options[2] && this.state.options[2].text} handleChange={() => {

                      }} /></div> : null
                    }
                   {
                     this.state.options[3] ?  <div className="mobile-checkbox-div"><ControlledCheckbox checked={this.state.options[3] && this.state.options[3].correct}
                     size={15}
                     labelStyle={{
                       marginLeft: 15, userSelect: "none", fontSize: '9px',
                       fontWeight: '500'
                     }}
                     borderRadius={2}
                     borderColor={"#04724d"}
                     label={this.state.options[3] && this.state.options[3].text} handleChange={() => {

                     }} /></div> : null
                   }
                  </div>

                </div>
                <Button
                  className="mobile-startInterviewButtonNew"
                  variant="contained"
                  onClick={this.handleInterview}

                >
                  Next Question  <ArrowForwardIcon className="mobile-arrowChanges"></ArrowForwardIcon>
                  <span></span>
                </Button>

              </div>

            </div>
          </div>
        </div>

      </div>
    );
  }
}


export default PreviewVerbalInterview;
