import React, { Component } from "react";
import Typography from "@material-ui/core/Typography";
import Button from "../Controls/Button/Button";
import "./previewInstructionsVerbalMobile.css";
import birballogo from "../../assets/images/Assessment/login_page_logo.png";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";
import { secondsToHours } from "../../utils/utility";



export class PreviewInstructionsMCQMobile extends Component {
  constructor(props) {
    super(props);

    this.state = {
      mcqQuestions:[],
      totalTime:''
       };
     }
   
     componentDidMount(){
       if( this.props.mcqQuestions){
      this.setState({
        mcqQuestions: this.props.mcqQuestions,
     
      })
   
      let totalTime=0
      this.props.mcqQuestions.map(item=>{
        totalTime+= item.id && item.id.suggestedDuration
      })
   
      this.setState({
        totalTime:totalTime
      })
     
         }
   
     }





  render() {


    return (
      <div className="mobile-instruction-main-div">
         
         <div className="mobile-main-wrapper">
         <div className="mobile-left-content">
          <div className="mobile-instructions-heading">
            <Typography>Instructions</Typography>
          </div>
          <div className="mobile-instructions-subHeading">
            <Typography>Read and follow the given Instructions</Typography>
          </div>
          </div>

          <div className="mobile-right-content">
          <div className="mobile-instructions-logo">
            <img src={birballogo}></img>
          </div>
          </div>
         </div>

          <div className="mobile-page-content">
            
            <div className="mobile-contents-verbal">
            <div>
        <div className="mobile-li-2">
         <div className="mobile-dot"></div> Question Type : MCQ
        </div>
        <div className="mobile-li-2">
        <div className="mobile-dot"></div>Total No. of Questions : {this.state.mcqQuestions.length}
        </div>
        <div className="mobile-li-2">
        <div className="mobile-dot"></div> Total Time : {secondsToHours(this.state.totalTime)}
        </div>
     
      </div>
  

            </div>

            <div  className="mobile-continue-button-wrapper">
                <Button className="mobile-continue-button"  disabled={true}>
                    Continue
                </Button>
            </div>

           
          </div>
          
      
        
      </div>
     
 
    );
  }
}

export default PreviewInstructionsMCQMobile;
