import React from "react";
import { TextField} from "@material-ui/core";
import InputAdornment from "@material-ui/core/InputAdornment";
import AccountCircle from "@material-ui/icons/AccountCircle";
import EmailIcon from "@material-ui/icons/Email";
import PhoneIcon from "@material-ui/icons/Phone";
import Button from "../../components/Controls/Button/Button";
import Logo from "../../assets/images/Assessment/login_page_logo.png";
import formlogo from "../../assets/images/Assessment/login-form-img.png";
import "./previewLogin.css";
import uploadIcon from '../../assets/images/Assessment/uploadIcon.png';
import caution from '../../assets/images/Assessment/caution.svg';


class Login extends React.Component {
  state = {
    email: "shubham@cronj.com",
    userName: "Shubham",
    phoneNumber: "987654321",
    pdf: "",

  
  };
  

  componentDidMount() {

  }



  render() {
  let cautionImage=<img  src={caution} />

    return (
         <div className="log-home">
     <div className="container">
        <div className="div-wrapper">
        <div className="div-1-wrapper">
          <div className="birbal-logo"> <img src={Logo} alt="logo" ></img></div>
           <div className="login_instr">
             <h6 className="mb-50">Basic Instructions</h6>
             < div className="list">
               <div className="point-wrapper">
                 <div className="bullet"></div>
                <div className="point-content"> <p >Please ensure that you are on a laptop or desktop.</p></div>
               </div>
               <div className="point-wrapper">
               <div className="bullet"></div>
                <div className="point-content"> <p >Ensure your internet connection is stable.</p></div>
               </div>
               <div className="point-wrapper">
               <div className="bullet"></div>
                <div className="point-content"> <p >
                   Check your headphones before using them. Your mic should
                   be working.
                 </p></div>
               </div>
               <div className="point-wrapper">
               <div className="bullet"></div>
                <div className="point-content"> <p >
                   Please ensure that the audio quality of your laptop /
                   desktop is good.
                 </p></div>
               </div>
             </div>
           </div>
         </div>
         <div className="div-2-wrapper">
         <div className="birbal-logo-new"> <img src={Logo} alt="logo" ></img></div>
           <div >
             <div className="login-form">
               <div className="img-login">
                 <img className="log-img" src={formlogo}></img>
                 <h2 className="login-bold-text">Login</h2>
               </div>
               <div className="card">
                 <form className="input-form">
                   <div class="form-group">
                     <label for="exampleFormControlInput1">Full Name</label>
                     <TextField id="standard-basic"  variant="outlined"
                     className="preview-text"
                       disabled="true"
                       value={this.state.userName}
                       onChange={(e) => this.handleChange(e, "userName")}
                       InputProps={{
                         startAdornment: (
                           <InputAdornment position="start">
                             <AccountCircle  style={{height:'15px',width:'15px'}}/>
                           </InputAdornment>
                         ),
                       }}
                     />
                   </div>
                   <div class="form-group">
                     <label for="exampleFormControlInput1">
                       Email
                     </label>
                     <TextField id="standard-basic"  variant="outlined"
                     className="preview-text"
                       type="email"
                       disabled="true"
                       value={this.state.email}
                       onChange={(e) => this.handleChange(e, "email")}
                       InputProps={{
                         startAdornment: (
                           <InputAdornment position="start">
                             <EmailIcon  style={{height:'15px',width:'15px'}} />
                           </InputAdornment>
                         ),
                       }}
                     />
                   </div>
                   <div class="form-group">
                     <label for="exampleFormControlInput1">Phone Number</label>
                     <TextField id="standard-basic"  variant="outlined"
                     className="preview-text"
                       value={this.state.phoneNumber}
                       disabled="true"
                       onChange={(e) => this.handleChange(e, "phoneNumber")}
                       InputProps={{
                         startAdornment: (
                           <InputAdornment position="start">
                             <PhoneIcon style={{height:'15px',width:'15px'}} />
                           </InputAdornment>
                         ),
                       }}
                     />
                   </div>

                   <div>
                   <div style={{fontSize:'11px',marginBottom:'2px'}}>  Resume</div>
                   
                   {/* <input id="fileInput" type="file" name="file" accept="application/pdf" onChange={(e)=>this.onFileChange(e)}/>
                    */}
                        <div className="upload-photo" style={{cursor:'pointer'}}   onClick={() =>
                  // `current` points to the mounted file input element
                  this.refs.fileUploader.click()
                }>
           <div className="flex-div"> 

           <img
                src={uploadIcon}
              
              />
                      <input
                      disabled={true}
               type="file"
               id="file"
               ref="fileUploader"
               style={{ display: "none" }}
               accept="application/pdf,application/msword,
               application/vnd.openxmlformats-officedocument.wordprocessingml.document"
                // onChange={(e)=>this.onFileChange(e)}
             />

             <div className="text-upload">Upload Your Resume </div>
             
              </div>

             </div>

             <div className="text-upload">{this.state.pdf && this.state.pdf.name ? this.state.pdf.name : null}</div>
             
                  {
                    this.state.pdfError ?  <div className="error"> 
                    {
                      this.state.pdfError 
                    }
                   </div> : null
                  }
                 {
                   this.state.submitting ?   <div style={{fontSize:'13px',color:'#7A7A7A'}}>
                     <div style={{display:'flex',justifyContent:'center',alignItems:'center',padding:'5px 0'}}> 
                   <div style={{marginRight:'5px'}}> 
                     {cautionImage}
                      </div>
                   <div>   {
                        this.state.submitting
                        
                      }
                      
                       </div>
                  </div>
                  <div style={{textAlign:'center'}}>{this.state.progress.toFixed(1)}%</div>
                      </div>
                   : null
                 }
                   </div>
                 </form>

                 <div className="button-div">
                   <Button
                     variant="contained"
                     className="Button-preview"
                     disabled={true}
                    //  onClick={() =>
                    //    this.buttonClick(
                    //      this.state.jdid,
                    //      this.state.ScheduleId,
                    //      this.state.uniqueId,
                    //      this.state.userID,
                    //      this.state.CandidateId
                    //    )
                    //  }
                   >
                     Login
                   </Button>
                 </div>
               </div>
             </div>
           </div>
         </div>
        </div>
      
     </div>
   </div>
    );
  }
}



export default Login;
