import React, { Component } from "react";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import Typography from "@material-ui/core/Typography";
import Button from "../Controls/Button/Button";
import "./previewInstructionsMobile.css";
import Step1 from "./Step1";
import birballogo from "../../assets/images/Assessment/login_page_logo.png";
import ArrowForwardIcon from "@material-ui/icons/ArrowForward";


function getSteps() {
  return ["1", "2", "3"];
}

export class Instructions extends Component {
  constructor(props) {
    super(props);

    this.state = {
      activeStep: 0,
      loaddd: false,
      step: 0,
      enable: false,
      loginDetails: "",
    };
  }



  setActiveStep(e) {
    this.setState({ activeStep: e });
  }

  changeHandler() {
    this.setState({ loaddd: !this.state.loaddd });
  }

  handleBack = () => {
    this.setState({ activeStep: this.state.activeStep - 1 });
  };


  handleReset = () => {
    this.setActiveStep(0);
  };

  handlePreview = () => {
    // this.props.history.push({
    //   pathname: "/preview",
    // });
  };

  handleStep = (step) => () => {
    if (step < this.state.activeStep) {
      this.setState({ activeStep: step });
    }
  };

  getStepContent(stepIndex) {
    switch (stepIndex) {
      case 0:
        return <Step1 className="li-mobile"/>;
      case 1:
        // return <Step2 />;
      case 2:
        // return <Step3 />;
      // case 3:
      // 	return <Step4 />;
      default:
        return "Unknown stepIndex";
    }
  }

  render() {
    const steps = getSteps();

    return (
      <div className="main-mobile">
      <div className="mobile2-instruction-main-div">
         
         <div className="mobile2-main-wrapper">
         <div className="mobile2-left-content">
          <div className="mobile2-instructions-heading">
            <Typography>Basic Instructions</Typography>
          </div>
          <div className="mobile2-instructions-subHeading">
            <Typography>Read and follow the given Instructions</Typography>
          </div>
          </div>

          <div className="mobile2-right-content">
          <div className="mobile2-instructions-logo">
            <img src={birballogo}></img>
          </div>
          </div>
         </div>

          <div className="mobile2-page-content">
            <div className="mobile2-mainstepper">
              <div>
                <Stepper activeStep={this.state.activeStep} alternativeLabel>
                  {steps.map((label, index) => (
                    <Step key={label} onClick={this.handleStep(index)}>
                      <StepLabel
                        iconContainerStyle={{ width: 72 }}
                        className="mobile2-step-label"
                      >
                        {label}
                      </StepLabel>
                    </Step>
                  ))}
                </Stepper>
              </div>
              <div>
                {this.state.activeStep === steps.length - 1 ? (
                  <Button
                    variant="contained"
                    className="mobile2-Button1 "
                    onClick={this.handlePreview}
                  >
                    Go to Preview
                  </Button>
                ) : (
                  <Button
                    variant="contained"
                    className="mobile2-Button "
                  
                  >
                    <span></span>
                    <span>Next</span>
                    <span>
                      {" "}
                      <ArrowForwardIcon className="mobile2-arrowChanges"></ArrowForwardIcon>
                    </span>
                  </Button>
                )}
              </div>
            </div>
            <div className="mobile2-contents">
            <div className="mobile2-mainstepper-mobile">
                <Stepper activeStep={this.state.activeStep} alternativeLabel>
                  {steps.map((label, index) => (
                    <Step key={label} onClick={this.handleStep(index)}>
                      <StepLabel
                        iconContainerStyle={{ width: 72 }}
                        className="mobile2-step-label"
                      >
                        {label}
                      </StepLabel>
                    </Step>
                  ))}
                </Stepper>
              </div>
              
              {this.state.activeStep === steps.length ? null : (
               
                  
                  <div>{this.getStepContent(this.state.activeStep)}</div>
               
              )}


            </div>

           
          </div>
          
      
        
      </div>
      <div className="mobile2-button-new">
                {this.state.activeStep === steps.length - 1 ? (
                  <Button
                    variant="contained"
                    className="mobile2-Button1 "
                    onClick={this.handlePreview}
                    
                  >
                    Go to Preview
                  </Button>
                ) : (
                  <Button
                    variant="contained"
                    className="mobile2-ButtonNew"
                    disabled={true}
                   
                  >
                    <span></span>
                    <span>Next</span>
                    <span>
                      {" "}
                      <ArrowForwardIcon className="mobile2-arrowChanges"></ArrowForwardIcon>
                    </span>
                  </Button>
                )}
              </div>
         
      </div>
    );
  }
}

export default Instructions;
