import React, { Component } from "react";
import "./previewCodingInterview.scss";
import Button from "@material-ui/core/Button";
import SelectInput from "../Atoms/customSelect/customSelect";
// import audio from "../../assets/images/audio.png";
import PreviewImage from '../../assets/images/Assessment/PreviewImage.png';
import CircularProgress from "@material-ui/core/CircularProgress";
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";

class CodeEditor extends Component {
  constructor(props) {
    super(props);
    this.state = {
 
    };
   
  }
  componentDidMount() {
  }
  CircularProgressWithLabel = (props) => {
    return (
      <Box position="relative" display="inline-flex">
        <CircularProgress variant="determinate" value={75} />
        <Box
          top={0}
          left={0}
          bottom={0}
          right={0}
          position="absolute"
          display="flex"
          alignItems="center"
          justifyContent="center"
        >
          <Typography
            variant="caption"
            component="div"
            style={{ color: '#04724d', fontWeight: '600', fontSize: '15px' }}
          >{`0:25`}</Typography>
        </Box>
      </Box>
    );
  }



  render() {
    return (
      <div className="CodingMainPage">
        <div className="CodingArea">
          <div className="QuestionArea">
            <div className="TitleQuestion">
              <div className="textTitle">Coding Question 1/2</div>
            </div>
            <div className="QuestionContent">
              <div className="QuestionMain">
               {
                        <div   dangerouslySetInnerHTML={{ __html:    this.props.codingQuestions && this.props.codingQuestions[0].id && this.props.codingQuestions[0].id.text }}></div>
               
               }
              </div>
            
            </div>
          </div>
          <div className="EditorArea">
            <div className="TitleQuestion">
              <div className="dropdownDiv">
                <SelectInput
                  className="LangauageSelect"
                  placeholder={"Python"
                  }
                  disabled={true}
                  options={[]}
                />
              </div>
              <div style={{ marginRight: "20px" }}>
                <Button
                  className="buttonCode"
                  variant="contained"
                  disabled={true}
                >
                  Run 
                  {/* <img src={codeIcon} alt="code icon" /> */}
                </Button>
              </div>
            </div>
           <div className="ace-editor">

           </div>
          </div>
        </div>
        <div className="OtherArea">
          <div className="TimerVideoArea">
            <div className="TimerDiv">
              <div className="TitleQuestion">
                <div className="textTitle">Timer</div>
              </div>
              <div className="TimerShow">
               <div className="timer-div">
               <div className="timer-wrapper">
              {this.CircularProgressWithLabel()}
            </div>
               </div>
              </div>
            </div>
            <div className="VideoDiv">
              <div className="blank-div-new">
              <img src={PreviewImage} />
              </div>
              <div className="audioDiv">
                <div className="audio-img">
                  {/* <img src={audio} alt="" /> */}
                </div>
                  <div className="audio-analyser">
                   
                  </div>

              </div>
            </div>
          </div>
          <div className="IOArea">
            <div className="IoHeader">
              <div className="TabSwitch">
                <div
                  className="Tab"
                  style={{
                    backgroundColor:  "#CADEDB" ,
                    color: "#21745D" ,
                    borderTopLeftRadius: "4px",
                  }}
                  onClick={() => this.setState({ tab: true })}
                >
                  Stdin
                </div>
                <div
                  className="Tab"
                  style={{
                    backgroundColor:  "#CADEDB" ,
                    color: ! "#21745D" ,
                  }}
                  
                >
                  Output Terminal
                </div>
              </div>
              <div style={{ marginRight: "20px" }}>
                <Button
                  className="buttonCode"
                  variant="contained"
                
                >
                  Submit
                </Button>
              </div>
            </div>
            <div className="IOtextArea">
                <textarea
                  placeholder="Enter Input Here"
                  className="TextareaInput"
                 disabled={true}
                ></textarea>
               
            
            </div>
          </div>
        </div>
      </div>
    );
  }
}


export default CodeEditor