import React from "react";
import { TextField} from "@material-ui/core";
import InputAdornment from "@material-ui/core/InputAdornment";
import AccountCircle from "@material-ui/icons/AccountCircle";
import EmailIcon from "@material-ui/icons/Email";
import PhoneIcon from "@material-ui/icons/Phone";
import Button from "../../components/Controls/Button/Button";
import Logo from "../../assets/images/Assessment/login_page_logo.png";
import formlogo from "../../assets/images/Assessment/login-form-img.png";
import "./previewLoginMobile.css";
import uploadIcon from '../../assets/images/Assessment/uploadIcon.png';
import caution from '../../assets/images/Assessment/caution.svg';

class LoginMobile extends React.Component {
    state = {
        email: "shubham@cronj.com",
        userName: "Shubham",
        phoneNumber: "987654321",
        pdf: "",
    
      
      };





  render() {
  let cautionImage=<img  src={caution} />

    return (
     !this.state.pageDisabled ?  <div className="mobile-log-home">
     <div className="mobile-container">
        <div className="mobile-div-wrapper">
        <div className="mobile-div-1-wrapper">
          <div className="mobile-birbal-logo"> <img src={Logo} alt="logo" ></img></div>
           <div className="mobile-login_instr">
             <h6 className="mobile-mb-50">Basic Instructions</h6>
             < div className="mobile-list">
               <div className="mobile-point-wrapper">
                 <div className="mobile-bullet"></div>
                <div className="mobile-point-content"> <p >Please ensure that you are on a laptop or desktop.</p></div>
               </div>
               <div className="mobile-point-wrapper">
               <div className="mobile-bullet"></div>
                <div className="mobile-point-content"> <p >Ensure your internet connection is stable.</p></div>
               </div>
               <div className="mobile-point-wrapper">
               <div className="mobile-bullet"></div>
                <div className="mobile-point-content"> <p >
                   Check your headphones before using them. Your mic should
                   be working.
                 </p></div>
               </div>
               <div className="mobile-point-wrapper">
               <div className="mobile-bullet"></div>
                <div className="mobile-point-content"> <p >
                   Please ensure that the audio quality of your laptop /
                   desktop is good.
                 </p></div>
               </div>
             </div>
           </div>
         </div>
         <div className="mobile-div-2-wrapper">
         <div className="mobile-birbal-logo-new"> <img src={Logo} alt="logo" ></img></div>
           <div >
             <div className="mobile-login-form">
               <div className="mobile-img-login">
                 <img className="mobile-log-img" src={formlogo}></img>
                 <h2 className="mobile-login-bold-text">Login</h2>
               </div>
               <div className="mobile-card">
                 <form className="mobile-input-form">
                
                     <label for="exampleFormControlInput1">Full Name</label>
                     <TextField
                       type="text"
                       disabled="true"
                       className='form-control-new'
                       id="exampleFormControlInput1"
                       value={this.state.userName}
                       onChange={(e) => this.handleChange(e, "userName")}
                       InputProps={{
                         startAdornment: (
                           <InputAdornment position="start">
                             <AccountCircle />
                           </InputAdornment>
                         ),
                       }}
                     />
                
                 
                     <label for="exampleFormControlInput1">
                       Email
                     </label>
                     <TextField
                       type="email"
                       className='form-control-new'
                       disabled="true"
                       id="exampleFormControlInput1"
                       value={this.state.email}
                       onChange={(e) => this.handleChange(e, "email")}
                       InputProps={{
                         startAdornment: (
                           <InputAdornment position="start">
                             <EmailIcon />
                           </InputAdornment>
                         ),
                       }}
                     />
                  
                
                     <label for="exampleFormControlInput1">Phone Number</label>
                     <TextField
                       type="text"
                       id="exampleFormControlInput1"
                       className='form-control-new'
                       value={this.state.phoneNumber}
                       disabled="true"
                       onChange={(e) => this.handleChange(e, "phoneNumber")}
                       InputProps={{
                         startAdornment: (
                           <InputAdornment position="start">
                             <PhoneIcon />
                           </InputAdornment>
                         ),
                       }}
                     />
               

                   <div style={{fontSize:'13px',fontWeight:'600'}}>
                     Resume
                     <br />
                   {/* <input id="fileInput" type="file" name="file" accept="application/pdf" onChange={(e)=>this.onFileChange(e)}/>
                    */}
                        <div className="mobile-upload-photo" style={{cursor:'pointer'}}   onClick={() =>
                  // `current` points to the mounted file input element
                  this.refs.fileUploader.click()
                }>
           <div className="mobile-flex-div"> 

           <img
                src={uploadIcon}
              
              />
                      <input
                       disabled={true}
               type="file"
               id="file"
               ref="fileUploader"
               style={{ display: "none" }}
               accept="application/pdf,application/msword,
               application/vnd.openxmlformats-officedocument.wordprocessingml.document"
                onChange={(e)=>this.onFileChange(e)}
             />

             <div className="mobile-text-upload">Upload Your Resume </div>
             
              </div>

             </div>

             <div className="mobile-text-upload">{this.state.pdf && this.state.pdf.name ? this.state.pdf.name : null}</div>
             
                  {
                    this.state.pdfError ?  <div className="mobile-error"> 
                    {
                      this.state.pdfError 
                    }
                   </div> : null
                  }
                 {
                   this.state.submitting ?   <div style={{fontSize:'13px',color:'#7A7A7A'}}>
                     <div style={{display:'flex',justifyContent:'center',alignItems:'center',padding:'5px 0'}}> 
                   <div style={{marginRight:'5px'}}> 
                     {cautionImage}
                      </div>
                   <div>   {
                        this.state.submitting
                        
                      }
                      
                       </div>
                  </div>
                  <div style={{textAlign:'center'}}>{this.state.progress.toFixed(1)}%</div>
                      </div>
                   : null
                 }
                   </div>
                 </form>

                 <div className="mobile-button-div">
                   <Button
                     variant="contained"
                     className="mobile-Button"
                     disabled={true}
                     onClick={() =>
                       this.buttonClick(
                         this.state.jdid,
                         this.state.ScheduleId,
                         this.state.uniqueId,
                         this.state.userID,
                         this.state.CandidateId
                       )
                     }
                   >
                     Login
                   </Button>
                 </div>
               </div>
             </div>
           </div>
         </div>
        </div>
      
     </div>
   </div> : this.state.mobile==false ?  <div>Please use a Laptop/PC to continue with the Interview process!</div> : this.state.laptop==false ? <div>Please use a Mobile/Tab to continue with the Interview process!</div> : null
    );
  }
}



export default LoginMobile;
