import React from "react";
import "./mainSteps.scss";
import Stepper from "../../../components/Atoms/Stepper";
// import Preview from '../Preview/Preview';
// import TypeOfQuestions from "../TypeOfQuestions/TypeOfQuestions";
// import Summary from "../Summary/Summary";
// import Publish from "../Publish/Publish";

class MainSteps extends React.Component {
  state = {
    activeStep: 0,
  };

  componentDidMount() {
    if (this.props && this.props.activeStep) {
      this.setState({
        activeStep: this.props.activeStep,
      });
    }
  }

  handleStep = (step) => () => {
    // if(step < this.state.activeStep){
    // this.setState({activeStep:step});
    // }
  };

  getSteps = () => {
    return ["Create Assessment", "Assessment Flow", "Preview", "Publish"];
  };
  render() {
    return (
      <div>
        <div className='preview-stepper'>
          <Stepper
            getSteps={this.getSteps}
            activeStep={this.state.activeStep}
            handleStep={this.handleStep}
            labelClassName='step-label'
            className='previewMainstepper'
          />
        </div>
        {/* { this.state.activeStep==0 ? <TypeOfQuestions  /> :
             this.state.activeStep==1 ? <Summary  /> :
            this.state.activeStep==2 ?
            <Preview  /> :
            this.state.activeStep==3 ?
            <Publish  />
             : null
            } */}
      </div>
    );
  }
}

export default MainSteps;
